* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  width           : 85%;
  max-width       : 1200px;
  margin          : 0 auto;
  background-color: #f2f2f2;
}

input {
  font-size: 1.2rem;
  color    : gray;
  width    : 100%;
}

ol {
  margin : 0;
  padding: 0;
}

.containerGif {
  display        : flex;
  flex-wrap      : wrap;
  justify-content: space-evenly;
  align-items    : center;
  width          : 100%;
}

.card {
  border-radius   : 10px;
  margin          : 5px;
  background-color: #fff;
  width           : 30%;
  text-align      : center;
  overflow        : hidden;
  box-shadow      : 0px 0px 5px rgba(0, 0, 0, 0.4);
}

.card img {
  border-radius: 10px 10px 0 0;
  width        : 100%;
  height       : 180px;
  object-fit   : cover;
}